import React, { Component } from 'react';

import profile from './Pictures/IMG_2649.jpg';
import {
    Image,
    Row,
    Col
} from 'react-bootstrap';


class About extends Component {
    render() {
        return (
            <div className="main">
                <div className='container'>
                    <h3>About me</h3>
                    <Row>
                        <Col sm={6} md={6}> 
                            <Image src={profile} thumbnail responsive style={styles.profilePic}/>
                        </Col>
                        <Col sm={6} md={6} style={styles.bioText}>
                            
                                <p>I am a current undergraduate student majoring in Computer Science at the University of California, San Diego.</p>
                                <p>My main interests in the field of Computer Science are web/software development, user experience, design, and machine learning. I love learning new technology and programming languages!</p>
                                <p>On my freetime I like to sell yard sale/flea market finds on ebay with my dad, hiking, watching movies, and finding new restaurants.</p>
                                <p>I also enjoy watching soccer and basketball (Go Lakers, Arsenal, and Real Madrid).</p>  
                                <p>I am a firm believer that avocado makes anything better and that pineapple does not belong on pizza.</p>
                            
                        </Col>
                    </Row>
                
                </div>
            </div>

        );
    }


}

const styles = {
    profilePic: {
        overflow: 'hidden'
    },
    bioText: {
        fontSize: "18px"
    }
}


export default About;


