import React, { Component } from 'react';
import holistic from './Pictures/holistic.png';
import waitz from './Pictures/WaitzLogo.png';
import twitter from './Pictures/Twitter_Logo_Blue.png';
// For "card" elements for projects
import {
    Thumbnail,
    Row,
    Col
} from 'react-bootstrap';

// For linking projects
import {
    Link
  } from 'react-router-dom';

class Projects extends Component {
    render() {
        return (
            <div className="main">
            <div className="container">
            <h3>Below are some projects I have worked on and there are more to come!</h3>
                    <Row>
                    <div className="container">
                    <Col sm={3} md={3}>
                    <a href="https://ucsdwaitz.com/" target="_blank">
                        <div className="card" style={styles.card}>
                            <Thumbnail src={ waitz } alt="242x200" style={styles.thumbnail}></Thumbnail>
                            <div className="caption" style={styles.cardText}>
                                    <h2>Waitz Website</h2>
                                    <p>
                                        Developed Waitz's home website using Node.js, HTML, Bootstrap, and Javascript
                                    </p>
                                </div>
                        </div>
                    </a>
                    </Col>
                    <Col sm={3} md={3}>
                    <a href="https://github.com/COGS108/Pr_005/blob/master/FinalProject.ipynb" target="_blank">
                        <div className="card" style={styles.card}>
                            <Thumbnail src={ twitter } alt="242x200" style={styles.twitterPic}></Thumbnail>
                            <div className="caption" style={styles.cardText}>
                                    <h2>Twitter Activity Analysis</h2>
                                    <p>
                                    Analyzed characteristics of tweet activity associated with the aftermath of a mass shooting using Python, natural language process frameworks and other data science techniques.

                                    </p>
                                </div>
                        </div>
                    </a>
                    </Col>
                    <Col sm={3} md={3}>
                    <Link to="/Waitz">
                        <div className="card" style={styles.card}>
                            <Thumbnail src={ waitz } alt="242x200" style={styles.thumbnail}></Thumbnail>
                            <div className="caption" style={styles.cardText}>
                                    <h2>Waitz Web Portal</h2>
                                    <p>
                                    Built web application for Waitz clients to view real-time and past foot traffic data for their venues.

                                    </p>
                                </div>
                        </div>
                    </Link>
                    </Col>
                    <Col sm={3} md={3}>
                        <Link to="/HolisticProject">
                            <div className="card" style={styles.card}>
                                <Thumbnail src={ holistic } alt="242x200" style={styles.thumbnail}></Thumbnail>
                                <div className="caption" style={styles.cardText}>
                                    <h2>Holistic</h2>
                                    <p>
                                        Holistic is a smart planner that helps you break down and achieve
                                        your goals by giving you recommendations when you need them
                                    </p>
                                </div>
                            </div>
                        </Link>
                    </Col>
                    </div>                          
                    </Row>
            
            </div>
            </div>

        );
    }

}

const styles = {
    card: {
        borderStyle: "solid",
        borderColor: "#f2f2f2",
        boxShadow: "5px 10px #fcfcfc",
        // maxHeight: "100px"
    },
    thumbnail: {
        borderStyle: "none",
        height: "auto",
        width: "100%"
    },
    twitterPic: {
      borderStyle: "none",
      height: "75%",
      width: "75%",
      float: "center"  
    },
    cardText: {
        marginRight: "10px",
        marginLeft: "10px",
        color: "black"
    }
}


export default Projects;


